import React from 'react';
import { Link } from 'react-router-dom';
import './BackNav.css'; // Importing the external CSS file

const BackNav = () => {
  return (
    <nav className="BackNav-nav">
      <ul className="BackNav-ul">
        <li className="BackNav-li">
          <Link className="BackNav-link" to="/back/fotos">Manage Photos</Link>
        </li>
        <li className="BackNav-li">
          <Link className="BackNav-link" to="/back/carousel">Manage Carousel</Link>
        </li>
        <li className="BackNav-li">
          <Link className="BackNav-link" to="/back/alert">Manage Alert</Link>
        </li>
        <li className="BackNav-li">
          <Link className="BackNav-link" to="/back/openingsuren">Manage Openingstijden</Link>
        </li>
      </ul>
    </nav>
  );
};

export default BackNav;
